import React from 'react';
import { Avatar, IconButton, ListItem, ListItemAvatar, ListItemText, ListItemSecondaryAction, Menu, MenuItem, Typography } from '@material-ui/core';
import { EmojiEvents, MoreVert } from '@material-ui/icons'
import { FixedSizeList } from 'react-window';

class Asistentes extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			selectedAsistente: null
		}
	}

	shouldComponentUpdate(nextProps, nextState) {
		this.refs.list.forceUpdate();
		return true;
	}

	handleItemDelete = (id) => {
		this.props.onDelete(id);
	}

	handleItemEdit = (id) => {
		this.props.onEdit(id);
	}

	handleItemSelect = (id) => {
		const selectedAsistente = this.props.asistentes.find(asistente => asistente.id === id);
		this.setState({ selectedAsistente: selectedAsistente });
		this.props.onSelect(selectedAsistente);
		this.refs.list.forceUpdate();
	}

	handleSendSchedule = (id) => {
		this.props.onSendSchedule(id);
	}

	renderRow = (props) => {
		const { index, style } = props;
		const asistente = this.props.asistentes[index];
		const { selectedAsistente } = this.state;
		return (
			<Asistente
				key={asistente.id}
				id={asistente.id}
				style={style}
				name={asistente.name}
				ideatoonProjects={asistente.ideatoonProjects}
				isSelected={asistente.id === selectedAsistente?.id}
				studio={asistente.studio}
				timezone={asistente.timezone}
				onDelete={this.handleItemDelete}
				onEdit={this.handleItemEdit}
				onSelect={this.handleItemSelect}
				onSendSchedule={this.handleSendSchedule}
			/>
		);
	}

	render() {
		const { asistentes } = this.props;
		return (
			<FixedSizeList
				ref='list'
				itemSize={80}
				itemCount={asistentes.length}
				height={400}
			>
				{this.renderRow}
			</FixedSizeList>
		)
	}
}

class Asistente extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			anchorEl: null
		}
	}

	handleItemClick = () => {
		this.props.onSelect(this.props.id);
	}

	handleItemEdit = () => {
		this.props.onEdit(this.props.id);
		this.setState({ anchorEl: null });
	}

	handleItemDelete = () => {
		this.props.onDelete(this.props.id);
		this.setState({ anchorEl: null });
	}

	handleMenuOpen = (event) => {
		this.setState({ anchorEl: event.currentTarget });
	}

	handleMenuClose = () => {
		this.setState({ anchorEl: null });
	}

	handleSendSchedule = () => {
		this.props.onSendSchedule(this.props.id);
		this.setState({ anchorEl: null });
	}

	render() {
		return (
			<ListItem
				button
				dense
				selected={this.props.isSelected}
				ContainerProps={{ style: this.props.style }}
				ContainerComponent="div"
				onClick={this.handleItemClick}
			>
				<ListItemAvatar>
					{this.props.ideatoonProjects && this.props.ideatoonProjects.length > 0
						? <Avatar><EmojiEvents /></Avatar>
						: <Avatar></Avatar>
					}
				</ListItemAvatar>
				<ListItemText primary={this.props.name} primaryTypographyProps={{ noWrap: true }} secondary={
					<React.Fragment>
						<Typography variant="body2" noWrap>{this.props.studio}</Typography>
						<Typography variant="body2" noWrap>{this.props.ideatoonProjects && this.props.ideatoonProjects.length > 0 && 'Ideatoon: ' + this.props.ideatoonProjects.join(', ')}</Typography>
						<Typography variant="body2" noWrap>{this.props.timezone ?? 'No timezone'}</Typography>
					</React.Fragment>
				}
				/>
				<ListItemSecondaryAction>
					<IconButton edge="end" onClick={this.handleMenuOpen}>
						<MoreVert />
					</IconButton>
					<Menu anchorEl={this.state.anchorEl} keepMounted open={Boolean(this.state.anchorEl)} onClose={this.handleMenuClose}>
						<MenuItem onClick={this.handleItemEdit}>Editar</MenuItem>
						<MenuItem onClick={this.handleItemDelete}>Eliminar</MenuItem>
						<MenuItem onClick={this.handleSendSchedule}>Enviar Agenda</MenuItem>
					</Menu>
				</ListItemSecondaryAction>
			</ListItem>
		);
	}
}

export default Asistentes;