import React from 'react';
import { IconButton, Typography } from '@material-ui/core';
import BaseTable, { AutoResizer, Column } from 'react-base-table'
import 'react-base-table/styles.css'
import { Add, Event } from '@material-ui/icons'

class Appointments extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			columns: [],
			data: []
		}
	}

	getColumns = () => {
		const columns = [];
		columns.push({
			key: 'asistente',
			title: 'Asistente',
			dataKey: 'asistenteName',
			width: 150,
			cellRenderer: ({ cellData, rowData }) => {
				const appointments = this.props.appointments.filter(appointment => appointment.asistentes.find(asistente => asistente.id === rowData.asistenteId));				
				return (<Typography noWrap variant="body2">{`(${appointments.length}) ${cellData}`}</Typography>);
			},
			headerRenderer: () => {
				return (<div></div>);
			},
			frozen: Column.FrozenDirection.LEFT
		});
		for (let i = 0; i < this.props.ejecutivos.length; i++) {
			const ejecutivo = this.props.ejecutivos[i];
			columns.push({
				key: `ejecutivo${i}`,
				title: ejecutivo.name,
				dataKey: i,
				width: 45,
				cellRenderer: ({ columnIndex, rowData }) => {
					const rowDataIndex = columnIndex - 1;
					const appointmentId = rowData[rowDataIndex];
					if (appointmentId) {
						return (
							<IconButton edge="end" size="small" onClick={() => this.handleClickEditAppointment(appointmentId)}>
								<Event />
							</IconButton>);
					} else {
						return (
							<IconButton edge="end" size="small" onClick={() => this.handleClickAddAppointment(rowData.asistenteId, ejecutivo.id)}>
								<Add />
							</IconButton>)
					}
				},
				headerRenderer: ({ column }) => {
					return (<Typography noWrap variant="body2" style={{ "display": "inline-flex", "align-items": "center", "height": "130px", "width":"100%", "writing-mode": "vertical-rl", "transform": "rotate(180deg)" }}>{column.title}</Typography>);
				}
			});
		}
		return columns;
	}

	getData = () => {
		const data = [];
		for (let i = 0; i < this.props.asistentes.length; i++) {
			const asistente = this.props.asistentes[i];
			const row = { asistenteId: asistente.id, asistenteName: asistente.name };
			for (let j = 0; j < this.props.ejecutivos.length; j++) {
				const ejecutivo = this.props.ejecutivos[j];
				const appointment = this.getAppointment(asistente, ejecutivo);
				row[j] = appointment?.id;
			}
			data.push(row);
		}
		return data;
	}

	handleClickAddAppointment = (asistenteId, ejecutivoId) => {
		this.props.onAddAppointment(asistenteId, ejecutivoId);
	}

	handleClickEditAppointment = (id) => {
		this.props.onEditAppointment(id);
	}

	getAppointment = (asistente, ejecutivo) => {
		const asistenteId = asistente.id;
		const ejecutivoId = ejecutivo.id;
		return this.props.appointments.find(appointment =>
			appointment.asistentes.find(asistente => asistente.id === asistenteId) &&
			appointment.ejecutivos.find(ejecutivo => ejecutivo.id === ejecutivoId));
	}

	clearActiveCells = () => {
		const appointmentHeaderCells = Array.from(document.querySelectorAll('.appointment-cell.active'));
		appointmentHeaderCells.forEach(element => element.classList.remove('active'));
	}

	getHeaderCells(cell) {
		const result = [];
		const cellEjecutivoId = cell.dataset.ejecutivo;
		const colHeaders = this.getEjecutivoHeaderCells(cellEjecutivoId);
		Array.prototype.push.apply(result, colHeaders);
		const cellAsistenteId = cell.dataset.asistente;
		const rowHeaders = this.getAsistenteHeaderCells(cellAsistenteId);
		Array.prototype.push.apply(result, rowHeaders);
		return result;
	}

	getAsistenteHeaderCells(id) {
		const result = [];
		const rowHeaders = Array.from(document.querySelectorAll('tbody > tr > th.appointment-cell'));
		const rowHeader = rowHeaders.find(element => element.dataset.asistente === id);
		if (rowHeader) {
			result.push(rowHeader);
		}
		return result;
	}

	getEjecutivoHeaderCells(id) {
		const result = [];
		const colHeaders = Array.from(document.querySelectorAll('thead > tr > th.appointment-cell'));
		const colHeader = colHeaders.find(element => element.dataset.ejecutivo === id);
		if (colHeader) {
			result.push(colHeader);
		}
		return result;
	}

	handleMouseOver = (event) => {
		this.clearActiveCells();
		const headerCells = this.getHeaderCells(event.currentTarget);
		headerCells.forEach(element => element.classList.add('active'));
	}

	handleAsistenteMouseOver = asistente => {
		this.clearActiveCells();
		const colHeaders = Array.from(document.querySelectorAll('thead > tr > th.appointment-cell'));
		for (const colHeader of colHeaders) {
			const ejecutivo = this.props.ejecutivos.find(ejecutivo => ejecutivo.id === colHeader.dataset.ejecutivo);
			if ((asistente.cgi === true && ejecutivo.cgi === true) ||
				(asistente.latamTopArtistOrFreelanceTalent === true && ejecutivo.latamTopArtistsAndFreelanceTalent === true) ||
				(asistente.newIPs === true && ejecutivo.newIPs === true) ||
				(asistente.newIdeas === true && ejecutivo.newIdes === true) ||
				(asistente.stopMotion === true && ejecutivo.stopMotion === true) ||
				(asistente.topStudio === true && ejecutivo.topStudios === true) ||
				(asistente.twoD === true && ejecutivo.twoD === true) ||
				(asistente.threeD === true && ejecutivo.threeD === true) ||
				(asistente.videogames === true && ejecutivo.videogames === true)) {
				colHeader.classList.add('active');
			}
		}
	}

	handleEjecutivoMouseOver = ejecutivo => {
		this.clearActiveCells();
		const rowHeaders = Array.from(document.querySelectorAll('tbody > tr > th.appointment-cell'));
		for (const rowHeader of rowHeaders) {
			const asistente = this.props.asistentes.find(asistente => asistente.id === rowHeader.dataset.asistente);
			if ((asistente.cgi === true && ejecutivo.cgi === true) ||
				(asistente.latamTopArtistOrFreelanceTalent === true && ejecutivo.latamTopArtistsAndFreelanceTalent === true) ||
				(asistente.newIPs === true && ejecutivo.newIPs === true) ||
				(asistente.newIdeas === true && ejecutivo.newIdes === true) ||
				(asistente.stopMotion === true && ejecutivo.stopMotion === true) ||
				(asistente.topStudio === true && ejecutivo.topStudios === true) ||
				(asistente.twoD === true && ejecutivo.twoD === true) ||
				(asistente.threeD === true && ejecutivo.threeD === true) ||
				(asistente.videogames === true && ejecutivo.videogames === true)) {
				rowHeader.classList.add('active');
			}
		}
	}

	renderCell = (props) => {
		const { columnIndex, rowIndex, style } = props;
		var result = null;
		if (columnIndex > 0 && rowIndex === 0) {
			const ejecutivo = this.props.ejecutivos[columnIndex - 1];
			if (ejecutivo) {
				result = (<div
					className="appointment-cell"
					align='center'
					style={style}
					padding='6px'
					data-ejecutivo={ejecutivo.id}
					onMouseOver={() => this.handleEjecutivoMouseOver(ejecutivo)}
				>
					<span style={{ "writing-mode": "vertical-rl", "transform": "rotate(180deg)" }}>
						{ejecutivo.name}
					</span>
				</div>);
			}
		} else if (columnIndex == 0 && rowIndex > 0) {
			const asistente = this.props.asistentes[rowIndex - 1];
			if (asistente) {
				result = (<div
					className="appointment-cell"
					style={style}
					cursor='alias'
					data-asistente={asistente.id}
					onMouseOver={() => this.handleAsistenteMouseOver(asistente)}
				>
					{asistente.name}
				</div>);
			}
		} else if (columnIndex > 0 && rowIndex > 0) {
			const asistente = this.props.asistentes[rowIndex - 1];
			const ejecutivo = this.props.ejecutivos[columnIndex - 1];
			if (asistente && ejecutivo) {
				result = (<div
					style={style}>
					<IconButton edge="end" size="small" onClick={() => this.handleClickEditAppointment(asistente, ejecutivo)}>
						<Event />
					</IconButton>
				</div>
				)
			}
		}
		return result;
	}

	render() {
		const columns = this.getColumns();
		const data = this.getData();
		return (
			<AutoResizer>
        {({ width, height }) => (
					<BaseTable
						fixed
						columns={columns}
						data={data}
						height={height}
						headerHeight={150}
						width={width}
					/>
				)}
      </AutoResizer>)
	}
}

export default Appointments;