import React from 'react';
import { Box, Card, CardContent, CardHeader, Grid, IconButton, Modal } from '@material-ui/core';
import { Add } from '@material-ui/icons'
import moment from 'moment-timezone';
import 'moment/locale/es';
import Asistentes from './Asistentes'
import { AsistenteInputComponent } from './AsistenteInputComponent'
import { AppointmentsCalendar } from './AppointmentsCalendar'
import { getIdeatoonProjects, sortAlphabeticallyByDatetime } from './Utils'

class AsistentesPanel extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			ideatoonProjects: getIdeatoonProjects(this.props.asistentes),
			inputAsistente: null,
			inputAsistenteComponentIsOpened: false,
			modalBody: '',
			selectedAsistente: null,
			snackbarOpen: false
		}
		moment.locale('es');
	}

	handleClickAddAsistente = () => {
		this.setState({ inputAsistenteComponentIsOpened: true });
	}

	handleDeleteAsistente = (id) => {
		if (this.state.selectedAsistente?.id === id) {
			this.setState({ selectedAsistente: null });
		}
		this.props.onDeleteAsistente(id);
	}

	handleDeleteAppointment = (id) => this.props.onDeleteAppointment(id);

	handleEditAsistente = (id) => {
		const asistente = this.props.asistentes.find(element => element.id === id);
		this.setState({
			inputAsistente: asistente,
			inputAsistenteComponentIsOpened: true
		});
	}

	handleInputAsistenteClose = () => {
		this.setState({
			inputAsistenteComponentIsOpened: false,
			inputAsistente: null
		});
	}

	handleSelectAsistente = (asistente) => this.setState({ selectedAsistente: asistente });

	handleSendSchedule = (id) => {
		const asistente = this.props.asistentes.find(element => element.id === id);
		const filteredAppointments = this.props.appointments.filter(appointment => appointment.asistentes.find(appointmentAsistente => appointmentAsistente.id === id));
		const timezone = 'US/Pacific';
		let localTimezone = 'America/Mexico_City';
		if (asistente.timezone) {
			localTimezone = asistente.timezone;
		}
		const appointments = filteredAppointments.sort((a, b) => sortAlphabeticallyByDatetime(a, b));
		let text = '';
		let currentDate, currentTime;
		for (const appointment of appointments) {
			if (appointment.datetime && appointment.ejecutivos && appointment.asistentes && appointment.duration) {

				const currentDatetime = moment(appointment.datetime);
				const date = moment.tz(currentDatetime.format('YYYY-MM-DDTHH:mm'), timezone);
				if (!currentDate || currentDate !== date.format('dddd DD MMMM')) {
					currentDate = date.format('dddd DD MMMM');
					currentTime = null;
					text += currentDate.toUpperCase() + ': ';
					switch (date.format('ddd')) {
						case 'lun.':
							text += 'https://us02web.zoom.us/j/85425291494';
							break;
						case 'mar.':
							text += 'https://us02web.zoom.us/j/88445795504';
							break;
						case 'mié.':
							text += 'https://us02web.zoom.us/j/86767091338';
							break;
						case 'jue.':
							text += 'https://us02web.zoom.us/j/87665492231';
							break;
						case 'vie.':
							text += 'https://us02web.zoom.us/j/88943327711';
							break;
						case 'sáb.':
							text += 'https://us02web.zoom.us/j/83792773099';
							break;
					}
					text += '\r\n\r\n';
				}
				if (!currentTime || currentTime !== date.format('hh:mm A z (Z)')) {
					currentTime = date.format('hh:mm A z (Z)');
					text += '    ' + date.tz(localTimezone).format('hh:mm A') + ' | ' + localTimezone;
					text += '\r\n';
				}
				text += '\r\n';
				if (appointment.ejecutivos.length > 0) {
					text += '        Ejecutivos: ' + appointment.ejecutivos.map(ejecutivo => ejecutivo.name).join(' | ') + '\r\n';
				}
				if (appointment.asistentes.length > 0) {
					text += '        Asistentes: ' + appointment.asistentes.map(asistente => asistente.name).join(' | ') + '\r\n';
				}
				text += '        Duración: 15min' + '\r\n\r\n';
			} else {
				//console.out('Error en ' + appointment);
			}
		}
		const body = (<Box style={{ "background-color": "white" }}><textarea rows="30" cols="100">{text}</textarea></Box>);
		this.setState({ modalBody: body, snackbarOpen: true });
	}

	handleSnackbarClose = () => this.setState({ snackbarOpen: false });

	handleSubmitAsistente = (asistente) => this.props.onSubmitAsistente(asistente);

	render() {
		const { appointments, asistentes } = this.props;
		const { modalBody, selectedAsistente, snackbarOpen } = this.state;
		return (
			<Grid container spacing={2}>
				<AsistenteInputComponent
					open={this.state.inputAsistenteComponentIsOpened}
					ideatoonProjects={this.state.ideatoonProjects}
					asistente={this.state.inputAsistente}
					onClose={this.handleInputAsistenteClose}
					onSubmit={this.handleSubmitAsistente}
				/>
				<Modal
					open={snackbarOpen}
					onClose={this.handleSnackbarClose}
				>
					{modalBody}
				</Modal>
				<Grid item sm={4} md={4}>
					<Card>
						<CardHeader
							title={`Asistentes (${asistentes.length})`}
							action={
								<IconButton edget="end" onClick={this.handleClickAddAsistente}>
									<Add />
								</IconButton>
							}
						/>
						<CardContent>
							<Asistentes
								asistentes={asistentes}
								onDelete={this.handleDeleteAsistente}
								onEdit={this.handleEditAsistente}
								onSelect={this.handleSelectAsistente}
								onSendSchedule={this.handleSendSchedule}
							/>
						</CardContent>
					</Card>
				</Grid>
				<Grid item sm={8} md={8}>
					<Card>
						<CardHeader title="Calendario" />
						<CardContent>
							<AppointmentsCalendar
								appointments={appointments}
								asistente={selectedAsistente}
								onDeleteAppointment={this.handleDeleteAppointment}
							/>
						</CardContent>
					</Card>
				</Grid>
			</Grid>
		);
	}
}

export default AsistentesPanel;