import { openDB } from 'idb';

async function getOpenDb() {
	const db = await openDB('pixelatl', 1, {
		upgrade(db) {
			if (db.objectStoreNames.contains('asistentes') === false) {
				db.createObjectStore('asistentes', { keyPath: 'id' });
			}
			if (db.objectStoreNames.contains('ejecutivos') === false) {
				db.createObjectStore('ejecutivos', { keyPath: 'id' });
			}
			if (db.objectStoreNames.contains('availability') === false) {
				db.createObjectStore('availability', { keyPath: 'id' });
			}
			if (db.objectStoreNames.contains('appointments') === false) {
				db.createObjectStore('appointments', { keyPath: 'id' });
			}
		}
	});
	return db;
}

async function deleteAppointment(id) {
	const db = await getOpenDb();
	await db.delete('appointments', id);
}

async function deleteAsistente(id) {
	const db = await getOpenDb();
	await db.delete('asistentes', id);
}

async function deleteEjecutivo(id) {
	const db = await getOpenDb();
	await db.delete('ejecutivos', id);
	await db.delete('availability', id);
}

async function getAppointments() {
	const db = await getOpenDb();
	return await db.getAll('appointments');
}

async function getAvailability() {
	const db = await getOpenDb();
	return await db.getAll('availability');
}

async function getAsistentes() {
	const db = await getOpenDb();
	return await db.getAll('asistentes');
}

async function getEjecutivos() {
	const db = await getOpenDb();
	return await db.getAll('ejecutivos');
}

async function storeAsistente(asistente) {
	const db = await getOpenDb();
	await db.put('asistentes', asistente);
}

async function storeAppointment(appointment) {
	const db = await getOpenDb();
	await db.put('appointments', appointment);
}

async function storeAvailability(availability) {
	const db = await getOpenDb();
	await db.put('availability', availability);
}

async function storeEjecutivo(ejecutivo) {
	const db = await getOpenDb();
	await db.put('ejecutivos', ejecutivo);
}

async function cleanAppointments() {
	const db = await getOpenDb();
	const store = db.transaction('appointments', 'readwrite').store;
	let cursor = await store.openCursor();
	while (cursor) {
		console.log(cursor.key, cursor.value);
		const appointment = cursor.value;
		const asistentes = appointment.asistentes;
		const ejecutivos = appointment.ejecutivos;
		if (asistentes) {
			for (const asistente of asistentes) {
				if (asistente.appointments) {
					delete asistente.appointments;
				}
			}
		}
		if (ejecutivos) {
			for (const ejecutivo of ejecutivos) {
				if (ejecutivo.appointments) {
					delete ejecutivo.appointments;
				}
			}
		}
		await store.put(appointment);
		cursor = await cursor.continue();
	}
}

export { cleanAppointments }
export { deleteAppointment, getAppointments, storeAppointment }
export { getAvailability, storeAvailability }
export { deleteEjecutivo, getEjecutivos, storeEjecutivo };
export { deleteAsistente, getAsistentes, storeAsistente };