import React from 'react';
import { Box, Button, ButtonGroup, Card, CardContent, CardHeader, Grid, IconButton, Modal } from '@material-ui/core';
import { Add } from '@material-ui/icons'
import moment from 'moment-timezone';
import 'moment/locale/es';
import Ejecutivos from './Ejecutivos'
import { EjecutivoInputComponent } from './EjecutivoInputComponent'
import { AppointmentsCalendar } from './AppointmentsCalendar'
import { sortAlphabeticallyByDatetime } from './Utils'

class EjecutivosPanel extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			inputEjecutivo: null,
			inputEjecutivoComponentIsOpened: false,
			selectedEjecutivo: null, 
			modalBody: '',			
			snackbarOpen: false
		}
		moment.locale('es');
	}

	handleClickAddEjecutivo = () => this.setState({ inputEjecutivoComponentIsOpened: true });

	handleClickImport = () => {
		/*for (let i = 0; i < 100; i++) {
			const ejecutivo = {
				id: uuid(),
				name: `Ejecutivo ${i + 1}`,
				positionAtStudio: `Position ${i + 1}`,
				email: `${i + 1}@mail.com`
			};
			storeEjecutivo(ejecutivo);
		}
		for (let i = 0; i < 100; i++) {
			const asistente = {
				id: uuid(),
				name: `Asistente ${i + 1}`,
				email: `${i + 1}@mail.com`,
				studio: `Estudio ${i + 1}`
			};
			storeAsistente(asistente);
		}*/
	}

	handleClickExport = () => {
		let csvContent = "data:text/csv;charset=utf-8,";
		const headerRow = 'NAME,POSITION AT THE STUDIO,STUDIO NAME,Meet with with top studios,Meet with the latam rising studios,Meet with latam top artists & freelance talent,Find new IPs in development,Find new ideas that could become an IP';
		csvContent += headerRow + '\r\n';
		this.props.ejecutivos.forEach(ejecutivo => {
			const csvRow = `"${ejecutivo.name}","${ejecutivo.positionAtStudio}","${ejecutivo.studio}","${ejecutivo.topStudios ? 'X' : ''}","${ejecutivo.latamRisingStudios ? 'X' : ''}","${ejecutivo.latamTopArtistsAndFreelanceTalent ? 'X' : ''}","${ejecutivo.newIPs ? 'X' : ''}","${ejecutivo.newIdeas ? 'X' : ''}"`;
			csvContent += csvRow + '\r\n';
		});
		const encodedUri = encodeURI(csvContent);
		window.open(encodedUri);
	}

	handleDeleteEjecutivo = (id) => {
		if (this.state.selectedEjecutivo?.id === id) {
			this.setState({ selectedEjecutivo: null });
		}
		this.props.onDeleteEjecutivo(id);
	}

	handleEditEjecutivo = (id) => {
		const ejecutivo = this.props.ejecutivos.find(element => element.id === id);
		this.setState({
			inputEjecutivo: ejecutivo,
			inputEjecutivoComponentIsOpened: true
		});
	}

	handleInputEjecutivoClose = () => {
		this.setState({
			inputEjecutivoComponentIsOpened: false,
			inputEjecutivo: null
		});
	}

	handleSelectEjecutivo = (ejecutivo) => this.setState({ selectedEjecutivo: ejecutivo });	

	handleSendSchedule = (id) => {
		const ejecutivo = this.props.ejecutivos.find(element => element.id === id);
		const filteredAppointments = this.props.appointments.filter(appointment => appointment.ejecutivos.find(appointmentEjecutivo => appointmentEjecutivo.id === ejecutivo.id));
		const timezone = 'US/Pacific';
		let localTimezone = 'America/Mexico_City';
		if (ejecutivo.timezone) {
			localTimezone = ejecutivo.timezone;
		}
		const appointments = filteredAppointments.sort((a, b) => sortAlphabeticallyByDatetime(a, b));
		let text = '';
		let currentDate, currentTime;
		for (const appointment of appointments) {
			if (appointment.datetime && appointment.ejecutivos && appointment.asistentes && appointment.duration) {

				const currentDatetime = moment(appointment.datetime);
				const date = moment.tz(currentDatetime.format('YYYY-MM-DDTHH:mm'), timezone);
				if (!currentDate || currentDate !== date.format('dddd DD MMMM')) {
					currentDate = date.format('dddd DD MMMM');
					currentTime = null;
					text += currentDate.toUpperCase() + ': ';
					switch(date.format('ddd')) {
						case 'lun.':
							text += 'https://us02web.zoom.us/j/85425291494';
							break;
						case 'mar.':
							text += 'https://us02web.zoom.us/j/88445795504';
							break;
						case 'mié.':
							text += 'https://us02web.zoom.us/j/86767091338';
							break;
						case 'jue.':
							text += 'https://us02web.zoom.us/j/87665492231';
							break;
						case 'vie.':
							text += 'https://us02web.zoom.us/j/88943327711';
							break;
						case 'sáb.':
							text += 'https://us02web.zoom.us/j/83792773099';
							break;							
					}
					text += '\r\n\r\n';
				}
				if (!currentTime || currentTime !== date.format('hh:mm A z (Z)')) {
					currentTime = date.format('hh:mm A z (Z)');
					text += '    ' + date.tz(localTimezone).format('hh:mm A') + ' | ' + localTimezone;
					text += '\r\n';
				}
				text += '\r\n';
				if (appointment.ejecutivos.length > 0) {
					text += '        Ejecutivos: ' + appointment.ejecutivos.map(ejecutivo => ejecutivo.name).join(' | ') + '\r\n';
				}
				if (appointment.asistentes.length > 0) {
					text += '        Asistentes: ' + appointment.asistentes.map(asistente => asistente.name).join(' | ') + '\r\n';
				}
				text += '        Duración: 15min' + '\r\n\r\n';
			} else {
				//console.out('Error en ' + appointment);
			}
		}
		const body = (<Box style={{ "background-color": "white" }}><textarea rows="30" cols="100">{text}</textarea></Box>);
		this.setState({ modalBody: body, snackbarOpen: true });
	}

	handleSnackbarClose = () => this.setState({ snackbarOpen: false });

	handleSubmitAvailability = availability => this.props.onSubmitAvailability(availability);

	handleSubmitEjecutivo = ejecutivo => this.props.onSubmitEjecutivo(ejecutivo);	

	render() {
		const { appointments, ejecutivos } = this.props;
		const { inputEjecutivo, inputEjecutivoComponentIsOpened, modalBody, selectedEjecutivo, snackbarOpen } = this.state;
		return (
			<Grid container spacing={2}>
				<EjecutivoInputComponent
					open={inputEjecutivoComponentIsOpened}
					ejecutivo={inputEjecutivo}
					onClose={this.handleInputEjecutivoClose}
					onSubmit={this.handleSubmitEjecutivo}
				/>
				<Modal
					open={snackbarOpen}
					onClose={this.handleSnackbarClose}
				>
					{modalBody}
				</Modal>
				<Grid item sm={4} md={4}>
					<Card>
						<CardHeader
							title={`Ejecutivos (${ejecutivos.length})`}
							action={
								<IconButton edget="end" onClick={this.handleClickAddEjecutivo}>
									<Add />
								</IconButton>
							}
						/>
						<CardContent>
							<Ejecutivos
								ejecutivos={ejecutivos}
								onDelete={this.handleDeleteEjecutivo}
								onEdit={this.handleEditEjecutivo}
								onSelect={this.handleSelectEjecutivo}
								onSendSchedule={this.handleSendSchedule}
							/>
						</CardContent>
					</Card>
					<Box
						display="flex"
						justifyContent="flex-end"
						alignItems="center"
						style={{ "margin-top": "5px" }}
					>
						<ButtonGroup variant="text">
							{ false && <Button onClick={this.handleClickImport}>Importar</Button> }
							<Button onClick={this.handleClickExport}>Exportar</Button>
						</ButtonGroup>
					</Box>
				</Grid>
				<Grid item sm={8} md={8}>
					<Card>
						<CardHeader title="Calendario" />
						<CardContent>
							<AppointmentsCalendar
								appointments={appointments}								
								ejecutivo={selectedEjecutivo}
								onSubmitAvailability={this.handleSubmitAvailability}
							/>
						</CardContent>
					</Card>
				</Grid>
			</Grid>
		);
	}
}

export default EjecutivosPanel;