import React from 'react';
import { Button, Checkbox, Dialog, DialogActions, DialogContent, DialogTitle, FormControlLabel, TextField } from '@material-ui/core';
import Autocomplete from '@material-ui/lab/Autocomplete';
import moment from 'moment-timezone';
import 'moment/locale/es';

class AsistenteInputComponent extends React.Component {

	constructor(props) {
		super(props);
		moment.locale('es');
		this.state = AsistenteInputComponent.stateFromProps(props);
	}

	static getDerivedStateFromProps(props, state) {
		if (props.open === true && state.open === false) {
			return AsistenteInputComponent.stateFromProps(props);
		} else {
			return state;
		}
	}

	static stateFromProps = function (props) {
		const asistente = props.asistente;
		return {
			open: props.open,
			id: asistente?.id || null,
			name: asistente?.name || '',
			ideatoonProjects: asistente?.ideatoonProjects || [],
			ideatoonProjectOptions: props.ideatoonProjects,
			cgi: asistente?.cgi || false,
			latamTopArtistOrFreelanceTalent: asistente?.latamTopArtistOrFreelanceTalent || false,
			newIPs: asistente?.newIPs || false,
			newIdeas: asistente?.newIdeas || false,
			studio: asistente?.studio || '',
			stopMotion: asistente?.stopMotion || false,
			timezone: asistente?.timezone || '',
			topStudio: asistente?.topStudio || false,
			twoD: asistente?.twoD || false,
			threeD: asistente?.threeD || false,
			videogames: asistente?.videogames || false,
		};
	}

	handleCancel = () => {
		this.setState({ open: false });
		this.props.onClose();
	};

	handleCheckedChange = (event) => {
		this.setState({ [event.target.name]: event.target.checked });
	}

	handleFormSubmit = (event) => {
		event.preventDefault();
		this.props.onSubmit({
			id: this.state.id,
			name: this.state.name.trim(),
			ideatoonProjects: this.state.ideatoonProjects,
			cgi: this.state.cgi,
			latamTopArtistOrFreelanceTalent: this.state.latamTopArtistOrFreelanceTalent,
			newIPs: this.state.newIPs,
			newIdeas: this.state.newIdeas,
			stopMotion: this.state.stopMotion,
			studio: this.state.studio,
			timezone: this.state.timezone,
			topStudio: this.state.topStudio,
			twoD: this.state.twoD,
			threeD: this.state.threeD,
			videogames: this.state.videogames
		});
		this.setState({ open: false });
		this.props.onClose();
	};

	handleValueChange = (event) => {
		this.setState({ [event.target.name]: event.target.value });
	}

	handleMultipleValueChange = (name, value) => {
		this.setState({ [name]: value });
	}

	render() {
		const { name, ideatoonProjectOptions, ideatoonProjects, topStudio, cgi, latamTopArtistOrFreelanceTalent, newIPs, newIdeas, stopMotion, studio, twoD, threeD, timezone, videogames } = this.state;
		return (
			<Dialog disableBackdropClick={true} open={this.state.open} onClose={this.handleCancel}>
				<form onSubmit={this.handleFormSubmit}>
					<DialogTitle>Asistente</DialogTitle>
					<DialogContent>
						<TextField autoFocus fullWidth={true} label="Name" margin="dense" name="name" required type="text" value={name}
							onChange={this.handleValueChange} />
						<TextField fullWidth={true} label="Studio name" margin="dense" name="studio" type="text" value={studio}
							onChange={this.handleValueChange} />
						<Autocomplete
							multiple
							value={ideatoonProjects}
							onChange={(_event, value) => this.handleMultipleValueChange('ideatoonProjects', value)}
							selectOnFocus
							clearOnBlur
							handleHomeEndKeys
							options={ideatoonProjectOptions}
							freeSolo
							filterSelectedOptions
							renderInput={(params) => (
								<TextField {...params} label="Ideatoon project" margin="dense" />
							)}
						/>
						<Autocomplete
							value={timezone}
							onChange={(_event, value) => this.handleMultipleValueChange('timezone', value)}
							options={moment.tz.names()}
							filterSelectedOptions
							renderInput={(params) => (
								<TextField {...params} label="Timezone" margin="dense" />
							)}
						/>
						<FormControlLabel label="2D"
							control={<Checkbox checked={twoD} onChange={this.handleCheckedChange} name="twoD" />}
						/>
						<FormControlLabel label="3D"
							control={<Checkbox checked={threeD} onChange={this.handleCheckedChange} name="threeD" />}
						/>
						<FormControlLabel label="Videogames"
							control={<Checkbox checked={videogames} onChange={this.handleCheckedChange} name="videogames" />}
						/>
						<FormControlLabel label="Stop motion"
							control={<Checkbox checked={stopMotion} onChange={this.handleCheckedChange} name="stopMotion" />}
						/>
						<FormControlLabel label="CGI"
							control={<Checkbox checked={cgi} onChange={this.handleCheckedChange} name="cgi" />}
						/>
						<FormControlLabel label="Studio that provide services or work-from-hire"
							control={<Checkbox checked={topStudio} onChange={this.handleCheckedChange} name="topStudio" />}
						/>
						<FormControlLabel label="Latam top artist or freelance talent"
							control={<Checkbox checked={latamTopArtistOrFreelanceTalent} onChange={this.handleCheckedChange} name="latamTopArtistOrFreelanceTalent" />}
						/>
						<FormControlLabel label="New IPs in development"
							control={<Checkbox checked={newIPs} onChange={this.handleCheckedChange} name="newIPs" />}
						/>
						<FormControlLabel label="Rising and 'under the radar' studios"
							control={<Checkbox checked={newIdeas} onChange={this.handleCheckedChange} name="newIdeas" />}
						/>
					</DialogContent>
					<DialogActions>
						<Button onClick={this.handleCancel}>Cancelar</Button>
						<Button type="submit" color="primary">Aceptar</Button>
					</DialogActions>
				</form>
			</Dialog>
		);
	}
}

export { AsistenteInputComponent };