import React from 'react';
import { Button, Checkbox, Dialog, DialogActions, DialogContent, DialogTitle, FormControl, FormControlLabel, FormLabel, FormGroup, TextField } from '@material-ui/core';
import Autocomplete from '@material-ui/lab/Autocomplete';
import moment from 'moment-timezone';
import 'moment/locale/es';

class EjecutivoInputComponent extends React.Component {

	constructor(props) {
		super(props);
		moment.locale('es');
		this.state = EjecutivoInputComponent.stateFromProps(props);
	}

	static getDerivedStateFromProps(props, state) {
		if (props.open === true && state.open === false) {
			return EjecutivoInputComponent.stateFromProps(props);
		} else {
			return state;
		}
	}

	static stateFromProps = function (props) {
		const ejecutivo = props.ejecutivo;
		return {
			open: props.open || false,
			id: ejecutivo?.id || null,
			name: ejecutivo?.name || '',
			positionAtStudio: ejecutivo?.positionAtStudio || '',
			studio: ejecutivo?.studio || '',
			isJury: ejecutivo?.isJury || false,
			cgi: ejecutivo?.cgi || false,
			latamTopArtistsAndFreelanceTalent: ejecutivo?.latamTopArtistsAndFreelanceTalent || false,
			newIPs: ejecutivo?.newIPs || false,
			newIdeas: ejecutivo?.newIdeas || false,
			stopMotion: ejecutivo?.stopMotion || false,
			timezone: ejecutivo?.timezone || '',
			twoD: ejecutivo?.twoD || false,
			threeD: ejecutivo?.threeD || false,
			topStudios: ejecutivo?.topStudios || false,
			videogames: ejecutivo?.videogames || false
		};
	}

	handleCancel = () => {
		this.setState({ open: false });
		this.props.onClose();
	};

	handleCheckedChange = (event) => {
		this.setState({ [event.target.name]: event.target.checked });
	}

	handleFormSubmit = (event) => {
		event.preventDefault();
		this.props.onSubmit({
			id: this.state.id,
			name: this.state.name.trim(),
			positionAtStudio: this.state.positionAtStudio.trim(),
			studio: this.state.studio.trim(),
			isJury: this.state.isJury,
			cgi: this.state.cgi,
			latamTopArtistsAndFreelanceTalent: this.state.latamTopArtistsAndFreelanceTalent,
			newIPs: this.state.newIPs,
			newIdeas: this.state.newIdeas,
			stopMotion: this.state.stopMotion,
			topStudios: this.state.topStudios,
			timezone: this.state.timezone || '',
			twoD: this.state.twoD,
			threeD: this.state.threeD,
			videogames: this.state.videogames
		});
		this.setState({ open: false });
		this.props.onClose();
	}

	handleValueChange = (event) => {
		this.setState({ [event.target.name]: event.target.value });
	}

	handleMultipleValueChange = (name, value) => {
		this.setState({ [name]: value });
	}

	render() {
		const { name, positionAtStudio, studio, isJury, topStudios, cgi, latamTopArtistsAndFreelanceTalent, newIPs, newIdeas, stopMotion, twoD, threeD, timezone, videogames } = this.state;
		return (
			<Dialog disableBackdropClick={true} open={this.state.open} onClose={this.handleCancel}>
				<form onSubmit={this.handleFormSubmit}>
					<DialogTitle>Ejecutivo</DialogTitle>
					<DialogContent>
						<TextField autoFocus fullWidth={true} label="Name" margin="dense" name="name" required type="text" value={name}
							onChange={(event) => this.handleValueChange(event)} />
						<TextField fullWidth={true} label="Position at studio" margin="dense" name="positionAtStudio" required type="text" value={positionAtStudio}
							onChange={this.handleValueChange} />
						<TextField fullWidth={true} label="Studio name" margin="dense" name="studio" type="text" value={studio}
							onChange={this.handleValueChange} />
						<FormControlLabel label="Is Jury?"
							control={<Checkbox checked={isJury} onChange={this.handleCheckedChange} name="isJury" />}
						/>
						<Autocomplete
							value={timezone}
							onChange={(_event, value) => this.handleMultipleValueChange('timezone', value)}
							options={moment.tz.names()}
							filterSelectedOptions
							renderInput={(params) => (
								<TextField {...params} label="Timezone" margin="dense" />
							)}
						/>
						<FormControlLabel label="2D"
							control={<Checkbox checked={twoD} onChange={this.handleCheckedChange} name="twoD" />}
						/>
						<FormControlLabel label="3D"
							control={<Checkbox checked={threeD} onChange={this.handleCheckedChange} name="threeD" />}
						/>
						<FormControlLabel label="Videogames"
							control={<Checkbox checked={videogames} onChange={this.handleCheckedChange} name="videogames" />}
						/>
						<FormControlLabel label="Stop Motion"
							control={<Checkbox checked={stopMotion} onChange={this.handleCheckedChange} name="stopMotion" />}
						/>
						<FormControlLabel label="CGI"
							control={<Checkbox checked={cgi} onChange={this.handleCheckedChange} name="cgi" />}
						/>
						<FormControl component="fieldset" margin="dense" fullWidth={true}>
							<FormLabel component="legend">Interests in the market</FormLabel>
							<FormGroup>
								<FormControlLabel label="Meet with studios that provide services or work-from-hire"
									control={<Checkbox checked={topStudios} onChange={this.handleCheckedChange} name="topStudios" />}
								/>
								<FormControlLabel label="Meet with latam top artists & freelance talent"
									control={<Checkbox checked={latamTopArtistsAndFreelanceTalent} onChange={this.handleCheckedChange} name="latamTopArtistsAndFreelanceTalent" />}
								/>
								<FormControlLabel label="Find new IPs in development"
									control={<Checkbox checked={newIPs} onChange={this.handleCheckedChange} name="newIPs" />}
								/>
								<FormControlLabel label="Discover talent: Rising and 'under the radar' studios"
									control={<Checkbox checked={newIdeas} onChange={this.handleCheckedChange} name="newIdeas" />}
								/>
							</FormGroup>
						</FormControl>
					</DialogContent>
					<DialogActions>
						<Button onClick={this.handleCancel}>Cancelar</Button>
						<Button type="submit" color="primary">Aceptar</Button>
					</DialogActions>
				</form>
			</Dialog>
		);
	}
}

export { EjecutivoInputComponent };