import React from 'react';
import { Box, Table, TableBody, TableCell, TableHead, TableRow } from '@material-ui/core';
import moment from 'moment-timezone';
import AppointmentInputComponent from './AppointmentInputComponent'

class AppointmentsCalendar extends React.Component {

	constructor(props) {
		super(props);
		const timezone = 'America/Mexico_City';
		const rows = [];
		for (let i = 0; i < 24 * 6; i++) {
			const totalMinutes = 10 * i;
			const isDisplayTimeEnabled = totalMinutes % 60 === 0;
			let displayTime = '';
			const minute = totalMinutes % 60;
			const hour = (totalMinutes - minute) / 60;
			if (isDisplayTimeEnabled === true) {
				if (hour === 0) {
					displayTime += '12';
				} else if (hour < 10) {
					displayTime += '0' + hour;
				} else {
					displayTime += hour;
				}
				if (minute !== 0) {
					displayTime += ':';
					if (minute === 0) {
						displayTime += '00'
					} else if (minute < 10) {
						displayTime += '0' + minute;
					} else {
						displayTime += minute;
					}
				}
			}
			const cells = [];
			const firstDay = moment.tz('2020-08-31', timezone);
			for (let day = 0; day < 6; day++) {
				const datetime = firstDay.clone().add(day, 'day').add(hour, 'hour').add(minute, 'minute').format();
				cells.push({ datetime });
			}
			rows.push({ displayTime, hour, cells });
		}
		this.state = {
			inputAppointment: null,
			inputAppointmentComponentIsOpened: false,
			rows: rows,
			timezone: timezone,
		};
	}

	componentDidUpdate(prevProps) {
		if ((!this.props.ejecutivo && !this.props.asistente) ||
			(prevProps.ejecutivo !== this.props.ejecutivo) ||
			(prevProps.asistente !== this.props.asistente) || 
			(prevProps.appointments !== this.props.appointments)) {
			this.clearActiveCells();
			this.clearAppointmentCells();
		}
		if (prevProps.ejecutivo !== this.props.ejecutivo) {
			this.clearAvailableCells();
			if (this.props.ejecutivo) {
				this.displayAvailability(this.props.ejecutivo.availability);
				this.displayEjecutivoAppointments();
			}
		}
		if (this.props.asistente) {
			this.displayAsistenteAppointments();
		}		
	}

	displayAvailability(availability) {
		document.querySelectorAll('td.calendar-cell').forEach(element => {
			availability.forEach(availability => {
				if (element.dataset.datetime === availability.datetime) {
					element.classList.add('available');
				}
			})
		});
	}

	displayAsistenteAppointments() {
		this.props.appointments.forEach(appointment => {
			if (appointment.asistentes.find(asistente => asistente.id === this.props.asistente.id)) {
				const calendarCells = Array.from(document.querySelectorAll('td.calendar-cell'));
				for (let i = 0; i < appointment.duration / 10; i++) {
					const datetime = moment(appointment.datetime).add(i * 10, 'minute').format();
					const calendarCell = calendarCells.find(cell => cell.dataset.datetime === datetime);
					if (calendarCell) {
						calendarCell.setAttribute('data-appointment-id', appointment.id);
						calendarCell.classList.add('appointment');
						if (appointment.ejecutivos.length === 0) {
							calendarCell.classList.add('extra');
						}
					}
				}
			}
		});
	}

	displayEjecutivoAppointments() {
		this.props.appointments.forEach(appointment => {
			if (appointment.ejecutivos.find(ejecutivo => ejecutivo.id === this.props.ejecutivo.id)) {
				const calendarCells = Array.from(document.querySelectorAll('td.calendar-cell'));
				for (let i = 0; i < appointment.duration / 10; i++) {
					const datetime = moment(appointment.datetime).add(i * 10, 'minute').format();
					const calendarCell = calendarCells.find(cell => cell.dataset.datetime === datetime);
					calendarCell.classList.add('appointment');
				}
			}
		});
	}

	getAdjacentCells(cell) {
		const cellMoment = moment(cell.dataset.datetime);
		const date = cellMoment.format('YYYY-MM-DD');
		const hour = cellMoment.format('H');
		const minute = cellMoment.format('m');
		const calendarCells = Array.from(document.querySelectorAll('td.calendar-cell'));
		return calendarCells.filter(element => {
			const elementMoment = moment(element.dataset.datetime);
			const elementDate = elementMoment.format('YYYY-MM-DD');
			const elementHour = elementMoment.format('H');
			const elementMinute = elementMoment.format('m');
			if (elementDate !== date) {
				return false;
			}
			if (elementHour !== hour) {
				return false;
			}
			if (minute < 30 && elementMinute < 30) {
				return true;
			} else if (minute >= 30 && elementMinute >= 30) {
				return true;
			}
			return false;
		});
	}

	getHeaderCells(cell) {
		const cellDate = moment(cell.dataset.datetime).format('YYYY-MM-DD');
		const cellHour = moment(cell.dataset.datetime).format('H');
		const result = [];
		const colHeaders = Array.from(document.querySelectorAll('thead > tr > th.calendar-cell'));
		const colHeader = colHeaders.find(element => element.dataset.date === cellDate);
		result.push(colHeader);
		const rowHeaders = Array.from(document.querySelectorAll('tbody > tr > th.calendar-cell'));
		const rowHeader = rowHeaders.find(element => element.dataset.hour === cellHour);
		result.push(rowHeader);
		return result;
	}

	getInputAvailability() {
		const availableCalendarCells = Array.from(document.querySelectorAll('td.calendar-cell.available'));
		const availability = availableCalendarCells.map((element) => {
			return { datetime: element.dataset.datetime }
		});
		availability.id = this.props.ejecutivo.id;
		return availability;
	}

	clearActiveCells = () => {
		const calendarCells = Array.from(document.querySelectorAll('.calendar-cell.active'));
		calendarCells.forEach(element => element.classList.remove('active'));
	}

	clearAppointmentCells = () => {
		const calendarCells = Array.from(document.querySelectorAll('.calendar-cell.appointment'));
		calendarCells.forEach(element => {
			element.removeAttribute('data-appointment-id');
			element.classList.remove('appointment');
		});
	}

	clearAvailableCells = () => {
		const calendarCells = Array.from(document.querySelectorAll('.calendar-cell.available'));
		calendarCells.forEach(element => element.classList.remove('available'));
	}

	handleClick = (event) => {
		if (this.props.ejecutivo) {
			const adjacentCells = this.getAdjacentCells(event.target);
			if (event.target.classList.contains('available') === true) {
				adjacentCells.forEach(element => element.classList.remove('available'));
			} else {
				adjacentCells.forEach(element => element.classList.add('available'));
			}
			const availability = this.getInputAvailability();
			this.props.onSubmitAvailability(availability);
		} else if (this.props.asistente) {
			const appointmentId = event.target.dataset.appointmentId;
			if (appointmentId) {
				this.setState({
					inputAppointment: this.props.appointments.find(appointment => appointment.id === appointmentId),
					inputAppointmentComponentIsOpened: true
				});
			}
		}
	}

	handleDeleteAppointment = (id) => this.props.onDeleteAppointment(id);	

	handleInputAppointmentClose = () => this.setState({ inputAppointmentComponentIsOpened: false });	

	handleMouseOver = (event) => {
		if (!this.props.ejecutivo) {
			return;
		}
		this.clearActiveCells();
		const adjacentCells = this.getAdjacentCells(event.target);
		adjacentCells.forEach(element => element.classList.add('active'));
		const headerCells = this.getHeaderCells(event.target);
		headerCells.forEach(element => element.classList.add('active'));
	}

	render() {
		return (
			<Box>
				<AppointmentInputComponent
					open={this.state.inputAppointmentComponentIsOpened}
					appointment={this.state.inputAppointment}
					appointments={this.props.appointments}
					onClose={this.handleInputAppointmentClose}
					onDelete={this.handleDeleteAppointment}
				/>
				<Table>
					<TableHead>
						<TableRow>
							<TableCell></TableCell>
							<TableCell className="calendar-cell" align="center" data-date="2022-09-05">Sep 5<br />Lunes</TableCell>
							<TableCell className="calendar-cell" align="center" data-date="2022-09-06">Sep 6<br />Martes</TableCell>
							<TableCell className="calendar-cell" align="center" data-date="2022-09-07">Sep 7<br />Miércoles</TableCell>
							<TableCell className="calendar-cell" align="center" data-date="2022-09-08">Sep 8<br />Jueves</TableCell>
							<TableCell className="calendar-cell" align="center" data-date="2022-09-09">Sep 9<br />Viernes</TableCell>
							<TableCell className="calendar-cell" align="center" data-date="2022-09-10">Sep 10<br />Sábado</TableCell>
						</TableRow>
					</TableHead>
					<TableBody onMouseLeave={this.clearActiveCells}>
						{this.state.rows.map((row, index) => (
							<TableRow key={index}>
								{row.displayTime.length > 0 &&
									<TableCell className="calendar-cell" rowSpan={6} component="th" scope="row" onMouseOver={this.clearActiveCells} data-hour={row.hour}>{row.displayTime}</TableCell>
								}
								{row.cells.map((cell, index) => (
									<TableCell key={index} className="calendar-cell" onClick={this.handleClick} onMouseOver={this.handleMouseOver} data-datetime={cell.datetime}></TableCell>
								))}
							</TableRow>
						))}
					</TableBody>
				</Table>
			</Box>
		);
	}
}

export { AppointmentsCalendar };