import React from 'react';
import { Box, Button, Grid, Snackbar } from '@material-ui/core';
import Appointments from './Appointments'
import AppointmentInputComponent from './AppointmentInputComponent'
import moment from 'moment-timezone';
import 'moment/locale/es';
import { getJurados, sortAlphabeticallyByDatetime } from './Utils'

class AppointmentsPanel extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			inputAppointment: null,
			inputAppointmentComponentIsOpened: false,
			jurados: getJurados(this.props.ejecutivos),
			selectedAsistente: null,
			selectedEjecutivo: null,
			snackbarOpen: false
		}
		moment.locale('es');
	}

	handleClickAddAppointment = (asistenteId, ejecutivoId) => {
		const asistente = this.props.asistentes.find(asistente => asistente.id === asistenteId);
		const ejecutivo = this.props.ejecutivos.find(ejecutivo => ejecutivo.id === ejecutivoId);
		this.setState({
			inputAppointmentComponentIsOpened: true,
			inputAsistente: asistente,
			inputEjecutivo: ejecutivo
		});
	}

	handleClickEditAppointment = (id) => {
		const appointment = this.props.appointments.find(appointment => appointment.id === id);
		this.setState({
			inputAppointmentComponentIsOpened: true,
			inputAppointment: appointment
		});
	}

	handleClickExport = () => {
		const timezone = 'America/Mexico_City';
		const appointments = this.props.appointments.filter(appointment => appointment.datetime).sort((a, b) => sortAlphabeticallyByDatetime(a, b));
		let text = '';
		let currentDate, currentTime;
		for (const appointment of appointments) {
			if (appointment.datetime && appointment.ejecutivos && appointment.asistentes && appointment.duration) {

				const currentDatetime = moment(appointment.datetime);
				const date = moment.tz(currentDatetime.format('YYYY-MM-DDTHH:mm'), timezone);
				if (!currentDate || currentDate !== date.format('dddd DD MMMM')) {
					currentDate = date.format('dddd DD MMMM');
					text += currentDate + '\r\n\r\n';
				}
				if (!currentTime || currentTime !== date.format('hh:mm A z (Z)')) {
					currentTime = date.format('hh:mm A z (Z)');
					text += '\t' + date.tz('America/Mexico_City').format('hh:mm A z (Z)');
					text += '\r\n';
				}
				text += '\r\n';
				if (appointment.ejecutivos.length > 0) {
					text += '\t\tEjecutivos: ' + appointment.ejecutivos.map(ejecutivo => ejecutivo.name).join(' | ') + '\r\n';
				}
				if (appointment.asistentes.length > 0) {
					text += '\t\tAsistentes: ' + appointment.asistentes.map(asistente => asistente.name).join(' | ') + '\r\n';
				}
				text += '\t\tDuración: ' + appointment.duration + 'min' + '\r\n\r\n';
			} else {
				//console.out('Error en ' + appointment);
			}
		}
		this.copy(text);
		this.setState({ snackbarOpen: true })
	}

	copy = (text) => {
		var textArea = document.createElement("textarea");
		textArea.value = text;

		// Avoid scrolling to bottom
		textArea.style.top = "0";
		textArea.style.left = "0";
		textArea.style.position = "fixed";

		document.body.appendChild(textArea);
		textArea.focus();
		textArea.select();

		try {
			var successful = document.execCommand('copy');
			var msg = successful ? 'successful' : 'unsuccessful';
			console.log('Fallback: Copying text command was ' + msg);
		} catch (err) {
			console.error('Fallback: Oops, unable to copy', err);
		}

		document.body.removeChild(textArea);
	}

	handleDeleteAppointment = (id) => {
		this.props.onDeleteAppointment(id);		
	}

	handleInputAppointmentClose = () => {
		this.setState({ inputAppointmentComponentIsOpened: false, inputAppointment: null });
	}

	handleSelectAsistente = (asistente) => {
		this.setState({ selectedAsistente: asistente });
	}

	handleSelectEjecutivo = (ejecutivo) => {
		this.setState({ selectedEjecutivo: ejecutivo });
	}

	handleSnackbarClose = () => {
		this.setState({ snackbarOpen: false });
	}

	handleSubmitAppointment = (appointment) => {
		this.props.onSubmitAppointment(appointment);		
	}

	render() {
		const { snackbarOpen } = this.state;
		const { appointments, asistentes, ejecutivos } = this.props;
		return (
			<Box>
				<AppointmentInputComponent
					open={this.state.inputAppointmentComponentIsOpened}
					appointment={this.state.inputAppointment}
					appointments={appointments}
					asistente={this.state.inputAsistente}
					asistentes={asistentes}
					ejecutivo={this.state.inputEjecutivo}
					ejecutivos={ejecutivos}
					onClose={this.handleInputAppointmentClose}
					onDelete={this.handleDeleteAppointment}
					onSubmit={this.handleSubmitAppointment} />
				<Snackbar
					open={snackbarOpen}
					onClose={this.handleSnackbarClose}
					autoHideDuration={3000}
					message="Agenda copiada"
				/>			
			<Grid container spacing={2} style={{ "height": "500px" }}>
				<Grid item xs={12} style={{ "height": "500px" }}>
					<Appointments
						appointments={appointments}
						asistentes={asistentes}
						ejecutivos={ejecutivos}
						onAddAppointment={this.handleClickAddAppointment}
						onEditAppointment={this.handleClickEditAppointment}
					/>				
				</Grid>
				<Grid item xd={12}>
					<Box
						display="flex"
						justifyContent="flex-end"
						alignItems="center"						
					>
						<Button style={{ "margin-top": "5px" }} onClick={this.handleClickExport}>
							Exportar
						</Button>
					</Box>
				</Grid>
			</Grid>
			</Box>
		);
	}
}

export default AppointmentsPanel;