import React from 'react';
import { Avatar, IconButton, ListItem, ListItemAvatar, ListItemText, ListItemSecondaryAction, Menu, MenuItem, Typography } from '@material-ui/core';
import { Gavel, MoreVert } from '@material-ui/icons'
import { FixedSizeList } from 'react-window';

class Ejecutivos extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			selectedEjecutivo: null
		}
	}

	shouldComponentUpdate(nextProps, nextState) {
		this.refs.list.forceUpdate();
		return true;
	}

	handleItemDelete = (id) => {
		this.props.onDelete(id);
	}

	handleItemEdit = (id) => {
		this.props.onEdit(id);
	}

	handleItemSelect = (id) => {
		const selectedEjecutivo = this.props.ejecutivos.find(ejecutivo => ejecutivo.id === id);
		this.setState({ selectedEjecutivo: selectedEjecutivo });
		this.props.onSelect(selectedEjecutivo);
		this.refs.list.forceUpdate();		
	}

	handleSendSchedule = (id) => {
		this.props.onSendSchedule(id);
	}

	renderRow = (props) => {
		const { index, style } = props;
		const ejecutivo = this.props.ejecutivos[index];
		const { selectedEjecutivo } = this.state;
		return (
			<Ejecutivo
				key={index}
				id={ejecutivo.id}
				style={style}
				name={ejecutivo.name}
				description={`${ejecutivo.positionAtStudio} @ ${ejecutivo.studio}`}
				timezone={ejecutivo.timezone}
				isJury={ejecutivo.isJury}
				isSelected={ejecutivo.id === selectedEjecutivo?.id}
				onDelete={this.handleItemDelete}
				onEdit={this.handleItemEdit}
				onSelect={this.handleItemSelect}
				onSendSchedule={this.handleSendSchedule}
			/>
		);
	}

	render() {
		const { ejecutivos } = this.props;
		return (
			<FixedSizeList
				ref='list'
				itemSize={80}
				itemCount={ejecutivos.length}
				height={400}
			>
				{this.renderRow}
			</FixedSizeList>
		)
	}
}

class Ejecutivo extends React.PureComponent {

	constructor(props) {
		super(props);
		this.state = {
			anchorEl: null
		}
	}

	handleItemClick = () => {
		this.props.onSelect(this.props.id);
	}

	handleItemEdit = () => {
		this.props.onEdit(this.props.id);
		this.setState({ anchorEl: null });
	}

	handleItemDelete = () => {
		this.props.onDelete(this.props.id);
		this.setState({ anchorEl: null });
	}

	handleMenuOpen = (event) => {
		this.setState({ anchorEl: event.currentTarget });
	}

	handleMenuClose = () => {
		this.setState({ anchorEl: null });
	}

	handleSendSchedule = () => {
		this.props.onSendSchedule(this.props.id);
		this.setState({ anchorEl: null });
	}

	render() {
		return (
			<ListItem
				button
				dense
				selected={this.props.isSelected}
				ContainerProps={{ style: this.props.style }}
				ContainerComponent="div"
				onClick={this.handleItemClick}
			>
				{this.props.isJury
					? <ListItemAvatar><Avatar><Gavel /></Avatar></ListItemAvatar>
					: <ListItemAvatar><Avatar alt={this.props.name} /></ListItemAvatar>}
				<ListItemText primary={this.props.name} secondary={
					<React.Fragment>
						<Typography variant="body2" noWrap>{this.props.description}</Typography>
						<Typography variant="body2" noWrap>{this.props.timezone ?? 'No timezone'}</Typography>						
					</React.Fragment>
				} />
				<ListItemSecondaryAction>
					<IconButton edge="end" onClick={this.handleMenuOpen}>
						<MoreVert />
					</IconButton>
					<Menu anchorEl={this.state.anchorEl} open={Boolean(this.state.anchorEl)} onClose={this.handleMenuClose}>
						<MenuItem onClick={this.handleItemEdit}>Editar</MenuItem>
						<MenuItem onClick={this.handleItemDelete}>Eliminar</MenuItem>
						<MenuItem onClick={this.handleSendSchedule}>Enviar Agenda</MenuItem>
					</Menu>
				</ListItemSecondaryAction>
			</ListItem>
		);
	}
}

export default Ejecutivos;