import React from 'react';
import { Button, Chip, Collapse, Dialog, DialogActions, DialogContent, DialogTitle, FormControl, InputLabel, TextField, MenuItem, ListSubheader, Select } from '@material-ui/core';
import { Gavel } from '@material-ui/icons'
import { Alert, Autocomplete } from '@material-ui/lab';
import moment from 'moment-timezone';
import 'moment/locale/es';

class AppointmentInputComponent extends React.Component {

	constructor(props) {
		super(props);
		moment.locale('es');
		this.state = AppointmentInputComponent.stateFromProps(props);
	}

	static getDerivedStateFromProps(props, state) {
		if (props.open === true && state.open === false) {
			return AppointmentInputComponent.stateFromProps(props);
		} else {
			return state;
		}
	}

	static stateFromProps = function (props) {
		const appointment = props.appointment;
		if (appointment) {
			let datetimeOptions = null;
			if (appointment.ejecutivos.length > 0) {
				const ejecutivoAppointments = props.appointments.filter(appointment => appointment.ejecutivos.find(ejecutivo => ejecutivo.id === appointment.ejecutivos[0].id));
				datetimeOptions = AppointmentInputComponent.getDatetimeOptionsForEjecutivo(appointment.ejecutivos[0], appointment.duration, ejecutivoAppointments)
			} else {
				datetimeOptions = AppointmentInputComponent.getDatetimeOptions();
			}
			return {
				open: props.open,
				openAlert: false,
				id: appointment.id,
				appointments: props.appointments,
				asistentes: appointment.asistentes,
				asistentesOptions: props.asistentes || appointment.asistentes,
				datetime: appointment.datetime,
				datetimeOptions: datetimeOptions,
				duration: appointment.duration,
				ejecutivos: appointment.ejecutivos,
				ejecutivosOptions: props.ejecutivos || appointment.ejecutivos
			};
		} else {
			let asistentes = [];
			if (props.asistente) {
				asistentes.push(props.asistente);
			}
			let ejecutivos = [];
			if (props.ejecutivo) {
				ejecutivos.push(props.ejecutivo);
			}
			let ejecutivoAppointments = [];
			if (props.appointments && ejecutivos.length > 0) {
				ejecutivoAppointments = props.appointments.filter(appointment => appointment.ejecutivos.find(ejecutivo => ejecutivo.id === ejecutivos[0].id));
			}
			const datetimeOptions =
				ejecutivos.length > 0
					? AppointmentInputComponent.getDatetimeOptionsForEjecutivo(ejecutivos[0], 30, ejecutivoAppointments)
					: AppointmentInputComponent.getDatetimeOptions();
			return {
				open: props.open,
				openAlert: false,
				id: null,
				appointments: props.appointments,
				asistentes: asistentes,
				asistentesOptions: props.asistentes,
				datetime: datetimeOptions.length > 0 && datetimeOptions[0].times.length > 0 ? datetimeOptions[0].times[0].value : null,
				datetimeOptions: datetimeOptions,
				duration: 20,
				ejecutivos: ejecutivos,
				ejecutivosOptions: props.ejecutivos
			};
		}
	}

	static getDatetimeOptions = function () {
		let datetimeOptions = [];
		const timezone = 'America/Mexico_City';
		const firstDay = moment.tz('2020-08-31', timezone);
		for (let day = 0; day < 6; day++) {
			const currentDay = firstDay.clone().add(day, 'day');
			const date = currentDay.format('YYYY-MM-DD');
			const datetimeOption = { date, times: [] };
			for (let hour = 8; hour <= 16; hour++) {
				for (let slot = 0; slot <= 6; slot++) {
					if (hour === 16 && slot >= 5) {
						continue;
					}
					const availabilityDatetime = firstDay.clone().add(day, 'day').add(hour, 'hour').add(slot * 10, 'minute');
					datetimeOption.times.push({ time: availabilityDatetime.format('HH:mm'), value: availabilityDatetime.format() });
				}
			}
			datetimeOptions.push(datetimeOption);
		}
		return datetimeOptions;
	}

	static getDatetimeOptionsForEjecutivo = function (ejecutivo, duration, ejecutivoAppointments) {
		let datetimeOptions = [];
		if (ejecutivo) {
			const dates = [];
			for (let availability of ejecutivo.availability) {
				const datetime = moment(availability.datetime);
				const date = datetime.format('YYYY-MM-DD');
				if (dates.includes(date) === false) {
					dates.push(date);
				}
			}
			dates.sort();
			datetimeOptions = dates.map(date => {
				const result = { date, times: [] };
				for (let availability of ejecutivo.availability) {
					const availabilityDatetime = moment(availability.datetime);
					const availabilityDate = availabilityDatetime.format('YYYY-MM-DD');
					if (availabilityDate === date) {
						const isAvailable = AppointmentInputComponent.isDatetimeAvailableForEjecutivo(ejecutivo, availabilityDatetime, duration, ejecutivoAppointments);
						if (isAvailable === true) {
							result.times.push({ time: availabilityDatetime.format('HH:mm'), value: availability.datetime });
						}
					}
				}
				return result;
			});
			datetimeOptions = datetimeOptions.filter(datetimeOption => datetimeOption.times.length > 0);
		}
		return datetimeOptions;
	}

	static isDatetimeAvailableForEjecutivo = function (ejecutivo, datetimeMoment, duration, ejecutivoAppointments, currentAppointmentId) {
		let isAvailable = true;
		for (let i = 0; i < duration / 10; i++) {
			const adjustedDatetimeMoment = datetimeMoment.clone().add(i * 10, 'minutes');
			const availableSlot = ejecutivo.availability.find(element => element.datetime === adjustedDatetimeMoment.format());
			if (!availableSlot) {
				isAvailable = false;
				break;
			}
			if (AppointmentInputComponent.isDatetimeBlockedByAppointments(ejecutivoAppointments, adjustedDatetimeMoment, currentAppointmentId) === true) {
				isAvailable = false;
				break;
			}
		}
		return isAvailable;
	}

	static isDatetimeBlockedByAppointments = function (appointments, datetimeMoment, currentAppointmentId) {
		let result = false;
		for (const appointment of appointments) {
			if (currentAppointmentId && appointment.id === currentAppointmentId) {
				continue;
			}
			const appointmentDatetime = moment(appointment.datetime);
			for (let i = 0; i < appointment.duration / 10; i++) {
				const adjustedAppointmentDateTime = appointmentDatetime.clone().add(i * 10, 'minutes');
				if (adjustedAppointmentDateTime.isSame(datetimeMoment) === true) {
					result = true;
					break;
				}
			}
			if (result === true) {
				break;
			}
		}
		return result;
	}

	getAsistenteAppointments = asistente => {
		const asistenteId = asistente.id;
		return this.props.appointments.filter(appointment => appointment.asistentes.find(asistente => asistente.id === asistenteId));
	}

	getAsistenteOptionStyle(asistente) {
		const style = {};
		const asistenteAppointments = this.getAsistenteAppointments(asistente)
		const isAvailable = AppointmentInputComponent.isDatetimeBlockedByAppointments(asistenteAppointments, moment(this.state.datetime), this.state.id) === false;
		if (isAvailable === true) {
			style['background-color'] = '#81c784';
		} else {
			style['background-color'] = '#e57373';
		}
		return style;
	}

	getEjecutivoAppointments = ejecutivo => {
		const ejecutivoId = ejecutivo.id;
		return this.props.appointments.filter(appointment => appointment.ejecutivos.find(ejecutivo => ejecutivo.id === ejecutivoId));
	}

	getEjecutivoOptionStyle(ejecutivo) {
		const style = {};
		const ejecutivoAppointments = this.getEjecutivoAppointments(ejecutivo);
		if (ejecutivo.availability) {
			const isAvailable = AppointmentInputComponent.isDatetimeAvailableForEjecutivo(ejecutivo, moment(this.state.datetime), this.state.duration, ejecutivoAppointments, this.state.id);
			if (isAvailable === true) {
				style['background-color'] = '#81c784';
			} else {
				style['background-color'] = '#e57373';
			}
		}
		return style;
	}

	handleCancel = () => {
		this.setState({ open: false });
		this.props.onClose();
	};

	handleDelete = () => {
		this.setState({ open: false });
		this.props.onClose();
		this.props.onDelete(this.state.id);
	};

	handleCheckedChange = (event) => {
		this.setState({ [event.target.name]: event.target.checked });
	}

	handleFormSubmit = (event) => {
		event.preventDefault();
		this.props.onSubmit({
			id: this.state.id,
			asistentes: this.state.asistentes,
			datetime: this.state.datetime,
			duration: this.state.duration,
			ejecutivos: this.state.ejecutivos
		});
		this.setState({ open: false });
		this.props.onClose();
	};

	handleValueChange = event => this.handleMultipleValueChange(event.target.name, event.target.value);

	handleMultipleValueChange = (name, value) => {
		this.setState(
			{ [name]: value },
			() => {
				let datetimeOptions = null;
				if (this.state.ejecutivos.length > 0) {
					const ejecutivoAppointments = this.props.appointments.filter(appointment => appointment.ejecutivos.find(ejecutivo => ejecutivo.id === this.state.ejecutivos[0].id));
					datetimeOptions = AppointmentInputComponent.getDatetimeOptionsForEjecutivo(this.state.ejecutivos[0], this.state.duration, ejecutivoAppointments)
				} else {
					datetimeOptions = AppointmentInputComponent.getDatetimeOptions();
				}
				let datetime = this.state.datetime;
				if (!datetimeOptions.find(datetimeOption => datetimeOption.times.find(time => time.value === datetime))) {
					datetime = datetimeOptions.length > 0 && datetimeOptions[0].times.length > 0 ? datetimeOptions[0].times[0].value : null;
				}
				this.setState({
					datetime: datetime,
					datetimeOptions: datetimeOptions
				});
			}
		);
	}

	makeSelectItems = (datetimeOptions) => {
		const items = [];
		for (let i = 0; i < datetimeOptions.length; i++) {
			const datetimeOption = datetimeOptions[i];
			items.push(<ListSubheader key={i} disableSticky>{datetimeOption.date}</ListSubheader>);
			for (let j = 0; j < datetimeOption.times.length; j++) {
				const datetimeOptionTime = datetimeOption.times[j];
				items.push(
					<MenuItem key={((i + 1) * 10) + j} value={datetimeOptionTime.value}>
						{datetimeOptionTime.time}
					</MenuItem>
				);
			}
		}
		return items;
	}

	render() {
		const { asistentes, asistentesOptions, datetime, datetimeOptions, duration, ejecutivos, ejecutivosOptions, id, openAlert } = this.state;
		return (
			<Dialog disableBackdropClick={true} open={this.state.open} onClose={this.handleCancel} fullWidth maxWidth="sm">
				<form onSubmit={this.handleFormSubmit}>
					<DialogTitle>Cita</DialogTitle>
					<DialogContent>
						<Autocomplete
							multiple
							value={asistentes}
							onChange={(_event, value) => this.handleMultipleValueChange('asistentes', value)}
							options={asistentesOptions}
							getOptionLabel={option => option.name}
							filterSelectedOptions
							fullWidth
							required
							renderInput={(params) => (
								<TextField {...params} label="Asistentes" margin="normal" />
							)}
							renderTags={(value, getTagProps) =>
								value.map((option, index) => (
									<Chip label={option.name} style={this.getAsistenteOptionStyle(option)} {...getTagProps({ index })} />
								))
							}
						/>
						<Autocomplete
							multiple
							value={ejecutivos}
							onChange={(_event, value) => this.handleMultipleValueChange('ejecutivos', value)}
							options={ejecutivosOptions}
							getOptionLabel={option => option.name}
							filterSelectedOptions
							fullWidth
							required
							renderInput={(params) => (
								<TextField {...params} label="Ejecutivos" margin="normal" />
							)}
							renderTags={(value, getTagProps) =>
								value.map((option, index) => (
									<Chip icon={option.isJury === true && <Gavel />} label={option.name} style={this.getEjecutivoOptionStyle(option)} {...getTagProps({ index })} />
								))
							}
						/>
						<TextField
							label="Duración (minutos)"
							type="number"
							name="duration"
							value={duration}
							onChange={this.handleValueChange}
							required
							margin="normal"
							inputProps={{
								min: 10,
								step: 10
							}}
							InputLabelProps={{
								shrink: true
							}}
						/>
						<FormControl fullWidth margin="normal">
							<InputLabel shrink>
								Fecha y hora
        			</InputLabel>
							<Select
								name="datetime"
								value={datetime}
								onChange={this.handleValueChange}
								renderValue={value => value ? moment(value).format('DD MMM, HH:mm') : ''}
							>
								{this.makeSelectItems(datetimeOptions)}
							</Select>
						</FormControl>
						<Collapse in={openAlert}>
							<Alert severity="error">This is an error alert — check it out!</Alert>
						</Collapse>
					</DialogContent>
					<DialogActions>
						{id && <Button onClick={this.handleDelete} color="secondary">Eliminar</Button>}
						<Button onClick={this.handleCancel}>Cancelar</Button>
						{this.props.onSubmit &&
							<Button type="submit" color="primary">Aceptar</Button>
						}
					</DialogActions>
				</form>
			</Dialog>
		);
	}
}

export default AppointmentInputComponent