import React from 'react';
import { AppBar, Box, Container, Paper, Tabs, Tab, Toolbar, Typography } from '@material-ui/core';
import { v4 as uuid } from 'uuid';
import AppointmentsPanel from './AppointmentsPanel'
import AsistentesPanel from './AsistentesPanel'
import EjecutivosPanel from './EjecutivosPanel'
import { getAvailability, getAppointments, getAsistentes, getEjecutivos } from './Repository';
import { deleteEjecutivo, storeEjecutivo } from './Repository'
import { deleteAsistente, storeAsistente } from './Repository'
import { cleanAppointments, deleteAppointment, storeAppointment } from './Repository'
import { storeAvailability } from './Repository'
import { sortAlphabeticallyByName } from './Utils'

class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			appointments: [],
			asistentes: [],
			ejecutivos: [],
			tabValue: 0
		}
	}

	componentDidMount() {

		//cleanAppointments();

		const promises = [];
		promises.push(getAppointments());
		promises.push(getAsistentes());
		promises.push(getAvailability());
		promises.push(getEjecutivos());
		Promise.all(promises).then(values => {

			const appointments = values[0];
			const asistentes = values[1];
			const availability = values[2];
			const ejecutivos = values[3];

			asistentes.sort((a, b) => sortAlphabeticallyByName(a, b));
			ejecutivos.sort((a, b) => sortAlphabeticallyByName(a, b));

			for (const ejecutivo of ejecutivos) {				
				const ejecutivoAvailability = availability.find(availability =>
					availability.id === ejecutivo.id
				);
				ejecutivo.availability = ejecutivoAvailability ?? [];
			}

			this.setState({
				appointments: appointments,
				asistentes: asistentes,
				ejecutivos: ejecutivos
			});
		});
	}

	handleDeleteAppointment = (id) => {
		deleteAppointment(id).then(() => {
			const appointments = this.state.appointments.filter(appointment => appointment.id !== id);
			this.setState({ appointments: appointments });
		});
	}

	handleDeleteAsistente = (id) => {
		deleteAsistente(id).then(() => {
			const asistentes = this.state.asistentes.filter(element => element.id !== id);
			this.setState({ asistentes: asistentes });
		});
	}

	handleDeleteEjecutivo = (id) => {
		deleteEjecutivo(id).then(() => {
			const ejecutivos = this.state.ejecutivos.filter(element => element.id !== id);
			this.setState({ ejecutivos: ejecutivos });
		});
	}

	handleSubmitAsistente = (asistente) => {
		let newAsistente = false;
		if (!asistente.id) {
			asistente.id = uuid();
			newAsistente = true;
		}
		let asistentes = this.state.asistentes;
		storeAsistente(asistente).then(() => {
			if (newAsistente === false) {
				asistentes = asistentes.filter(element => element.id !== asistente.id);
			}
			asistentes.push(asistente);
			asistentes.sort((a, b) => sortAlphabeticallyByName(a, b));
			this.setState({ asistentes: asistentes });
		});
	}

	handleSubmitAvailability = (availability) => {
		storeAvailability(availability).then(() => {
			const ejecutivo = this.state.ejecutivos.find(element => element.id === availability.id);
			ejecutivo.availability = availability;
			this.setState({ ejecutivos: this.state.ejecutivos });
		});
	}

	handleSubmitAppointment = (appointment) => {
		let newAppointment = false;
		if (!appointment.id) {
			appointment.id = uuid();
			newAppointment = true;
		}
		let appointments = this.state.appointments;
		storeAppointment(appointment).then(() => {
			if (newAppointment === false) {
				appointments = appointments.filter(element => element.id !== appointment.id);
			}
			appointments.push(appointment);
			this.setState({ appointments: appointments });
		});
	}

	handleSubmitEjecutivo = (ejecutivo) => {
		let newEjecutivo = false;
		if (!ejecutivo.id) {
			ejecutivo.id = uuid();
			newEjecutivo = true;
			ejecutivo.availability = [];
		}
		let ejecutivos = this.state.ejecutivos;
		storeEjecutivo(ejecutivo).then(() => {
			if (newEjecutivo === false) {
				ejecutivos = ejecutivos.filter(element => element.id !== ejecutivo.id);
			}			
			ejecutivos.push(ejecutivo);
			ejecutivos.sort((a, b) => sortAlphabeticallyByName(a, b));
			this.setState({ ejecutivos: ejecutivos });
		});
	}

	handleTabChange = (_event, newValue) => {
		this.setState({ tabValue: newValue });
	}

	render() {
		const { appointments, asistentes, ejecutivos, tabValue } = this.state;
		return (
			<Box>
				<AppBar position="static">
					<Toolbar>
						<Typography variant="h6">Pixelatl - Mercado</Typography>
					</Toolbar>
				</AppBar>
				<Container disableGutters={true} maxWidth={false}>
					<Paper style={{ marginBottom: 10 }}>
						<Tabs
							value={tabValue}
							onChange={this.handleTabChange}
							indicatorColor="primary"
							textColor="primary"
							centered
						>
							<Tab label="EJECUTIVOS" />
							<Tab label="ASISTENTES" />
							<Tab label="CITAS" />
						</Tabs>
					</Paper>
					<TabPanel value={tabValue} index={0}>
						<EjecutivosPanel
							appointments={appointments}
							ejecutivos={ejecutivos}
							onDeleteEjecutivo={this.handleDeleteEjecutivo}
							onSubmitAvailability={this.handleSubmitAvailability}
							onSubmitEjecutivo={this.handleSubmitEjecutivo}
						/>
					</TabPanel>
					<TabPanel value={tabValue} index={1}>
						<AsistentesPanel
							asistentes={asistentes}
							appointments={appointments}
							onDeleteAppointment={this.handleDeleteAppointment}
							onDeleteAsistente={this.handleDeleteAsistente}
							onSubmitAsistente={this.handleSubmitAsistente}
						/>
					</TabPanel>
					<TabPanel value={tabValue} index={2}>
						<AppointmentsPanel
							appointments={appointments}
							asistentes={asistentes}
							ejecutivos={ejecutivos}
							onDeleteAppointment={this.handleDeleteAppointment}
							onSubmitAppointment={this.handleSubmitAppointment}
						/>
					</TabPanel>
				</Container>
			</Box>
		)
	}
}

function TabPanel(props) {
	const { children, value, index, ...other } = props;
	return (
		<div
			role="tabpanel"
			hidden={value !== index}
			id={`full-width-tabpanel-${index}`}
			{...other}
		>
			{value === index && (
				<Box>
					{children}
				</Box>
			)}
		</div>
	);
}

export default App;