import moment from 'moment-timezone';
import 'moment/locale/es';

function getIdeatoonProjects(asistentes) {
	const ideatoonProjects = [...new Set(asistentes.filter(asistente => !!asistente.ideatoonProjects).flatMap(asistente => asistente.ideatoonProjects))];
	ideatoonProjects.sort((a, b) => sortAlphabetically(a, b));
	return ideatoonProjects;
}

function getJurados(ejecutivos) {
	const jurados = ejecutivos.filter(ejecutivo => ejecutivo.isJury === true);
	jurados.sort((a, b) => sortAlphabeticallyByName(a, b));
	return jurados;
}

function sortAlphabetically(a, b) {
	const nameA = a.toUpperCase();
	const nameB = b.toUpperCase();
	if (nameA < nameB) {
		return -1;
	}
	if (nameA > nameB) {
		return 1;
	}
	return 0;
}

function sortAlphabeticallyByDatetime(a, b) {
	if (!a.datetime) {
		return -1;
	}
	if (!b.datetime) {
		return -1;
	}
	var nameA = moment(a.datetime);
	var nameB = moment(b.datetime);
	if (nameA.isBefore(nameB) === true) {
		return -1;
	}
	if (nameB.isBefore(nameA) === true) {
		return 1;
	}
	return 0;
}

function sortAlphabeticallyByName(a, b) {
	const nameA = a.name.toUpperCase();
	const nameB = b.name.toUpperCase();
	if (nameA < nameB) {
		return -1;
	}
	if (nameA > nameB) {
		return 1;
	}
	return 0;
}

export { getIdeatoonProjects, getJurados, sortAlphabetically, sortAlphabeticallyByDatetime, sortAlphabeticallyByName };